%flex-centred {
    display: flex;
    justify-content: center;
    align-items: center;
}

%cubic {
    transition-property: background-color, color, border-color;
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1); }